import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter, Route, Switch, Link, useParams, useLocation, useHistory, useRouteMatch } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import SveikinimuSarasas from './components/Sveikinimas/SveikinimuSarasas';
import Edit from './components/Sveikinimas/Edit';

var AppContainer = (props) => {
  return (
  <header className="top-header">
<nav class="navbar header-nav navbar-expand-lg">
<div class="container">
<button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbar-wd" aria-controls="navbar-wd" aria-expanded="false" aria-label="Toggle navigation">
      <span></span>
      <span></span>
      <span></span>
    </button>
    <div class="collapse navbar-collapse justify-content-end" id="navbar-wd">

            
    <ul class="navbar-nav">
                <Link to='/'>Sveikinimų sąrašas</Link> 
  <li><Link to='/sveikinimu-sarasas'>Sveikinimo peržiūros langas su sąrašu</Link></li>
  <li><Link to={`/redagavimas`}>Sveikinimo pridėjimas/ redagavimas</Link></li>
  <li><Link to='/pridejimas'>Vietos pridėjimas</Link></li>
  <li> <Link to='/sveikinimai-pagal'>Sveikinimų sąrašas pagal adreso tipą</Link></li>

                </ul>
    </div>
</div>
</nav>
    {props.children}
    </header>);
};



var NoMatch = (props) => {
  var goApp = () => props.history.push("/");
  return <div>Route did not match
  <button onClick={goApp}>Go Home</button></div>;
};


var DemonstruotiNavigacija = (props) => {
  let goHome = () => props.history.push("/");
  let { id } = props.match.params;
  return (
    <div>
      At route: {props.location.pathname} (ID: {id})
      <button onClick={goHome}>Go Home</button>
      <pre>
        {JSON.stringify(props, null, 2)}
      </pre>
    </div>
  );
};

// eslint-disable-next-line no-redeclare
var DemonstruotiNavigacija = () => {
  let location = useLocation();
  let history = useHistory();
  let goHome = () => history.push("/");
  let params = useParams();
  let match = useRouteMatch();
  let { id } = useParams(); // arba match.params
  return (
    <div>
      At route: {location.pathname} (ID: {id})
      <button onClick={goHome}>Go Home</button>
      <pre>
        {JSON.stringify(history, null, 2)}<br />
        {JSON.stringify(location, null, 2)}<br />
        {JSON.stringify(match, null, 2)}<br />
        {JSON.stringify(params, null, 2)}
      </pre>
    </div>
  );
};


// ReactDOM.render((
//   <App />
// ), document.getElementById('root'));

ReactDOM.render((
  <BrowserRouter basename={process.env.PUBLIC_URL}>
    <AppContainer>
      <Switch>
        <Route exact path='/' component={App} />
        <Route path="/products/:id"
          component={DemonstruotiNavigacija} />
        <Route path="/sveikinimu-sarasas" component={SveikinimuSarasas} />
        <Route path="/redagavimas" component={Edit} />
        <Route path="*" component={NoMatch} />
        <Route component={NoMatch} />
      </Switch>
    </AppContainer>
  </BrowserRouter>
), document.getElementById('root'));




