import React from 'react';
import './ProductListContainer.css';
import { useHistory } from "react-router-dom";
import { Button } from 'reactstrap';
 



export default function ProductListContainer(props) {
    const history = useHistory();

const routeChange = () =>{ 
  let path = "/redagavimas"; 
  history.push(path);
}
    return (
        <div className="card">
            <img src="https://picsum.photos/200/300" className="card-img-top" alt="paveiksliukas" />
            <div classname="card-body">
                <h5 className="card-title">Mielai mociutei</h5>
                <p className="card-text">Sveikinimai</p>
                <Button color="primary" className="px-4"
                onClick={routeChange}> Redaguoti</Button>
            </div>
        </div>
    )
}